import React from "react";
import styled from "styled-components";
import classnames from "classnames";

import { compose } from "modules/utils";
import { withSocket } from "modules/net";
import { KeyboardController, TouchController } from "modules/controller";

import GameCanvas from "./GameCanvas";

const Timer = styled.div`
  position: fixed;
  top: 0.5rem;
  width: 100%;
  text-align: center;
  pointer-events: none;

  &.urgent {
    color: #aa3333;
  }
`;

const PlayersCounter = styled.div`
  position: fixed;
  top: 2rem;
  width: 100%;
  text-align: center;
  pointer-events: none;

  & .number {
    color: #aa3333;
  }
`;

class GamePage extends React.Component {
  state = {
    inputVel: { x: 0, y: 0 },
    map: null,
    gameState: null
  };

  async componentDidMount() {
    const { socket } = this.props;

    await new Promise((resolve, reject) => {
      if (socket.connected) return resolve();
      const timeout = setTimeout(reject, 3000);
      socket.once("connect", () => {
        clearTimeout(timeout);
        resolve();
      });
    });

    socket.on("game:update", gameState => {
      this.setState({ gameState });
    });

    socket.on("game:end", results => {
      this.setState({
        results,
        gameState: {
          ...this.state.gameState,
          remainingTime: 0
        }
      });
    });

    socket.emit("game:join", async (gameId, playerId, mapPath) => {
      const mapUrl = process.env.REACT_APP_GAME_HOST + mapPath;
      const map = await fetch(mapUrl).then(res => res.json());

      this.setState({
        map: Object.assign(map, {
          url: mapUrl,
          basePath: mapUrl.match(/^(.*\/)([^/]*)$/)[1]
        })
      });
    });

    this.playerUpdateInterval = setInterval(() => {
      socket.emit("player:update", { inputVel: this.state.inputVel });
    }, 1000 / 30);
  }

  componentWillUnmount() {
    clearInterval(this.playerUpdateInterval);
  }

  handleInputVelChange = inputVel => {
    this.setState({ inputVel });
  };

  handleInputAction = () => {};

  formatTimer(time) {
    const date = new Date(time);

    const pad0 = (value, l) => ("000" + value).slice(-l);

    const min = pad0(date.getUTCMinutes(), 2);
    const sec = pad0(date.getUTCSeconds(), 2);
    const msec = pad0(date.getUTCMilliseconds(), 3);

    return `${min}:${sec}.${msec}`;
  }

  render() {
    const { inputVel, map, gameState } = this.state;

    if (!map || !gameState) return null;

    return (
      <React.Fragment>
        <GameCanvas map={map} gameState={gameState} />

        <KeyboardController
          onInputVelChange={this.handleInputVelChange}
          onInputAction={this.handleInputAction}
          inputVel={inputVel}
        />
        <TouchController
          onInputVelChange={this.handleInputVelChange}
          onInputAction={this.handleInputAction}
          inputVel={inputVel}
        />

        <PlayersCounter className="GamePage-playersCount">
          Currently <span className="number">{gameState.players.length}</span>{" "}
          {gameState.players.length > 1 ? "players" : "player"}
        </PlayersCounter>

        <Timer
          className={classnames({
            urgent:
              gameState.remainingTime < 10000 &&
              !(Math.floor(gameState.remainingTime / 1000) % 2)
          })}
        >
          {this.formatTimer(gameState.remainingTime)}
        </Timer>
      </React.Fragment>
    );
  }
}

export default compose(withSocket())(GamePage);



