import React from "react";
import styled from "styled-components";

import {
  getTileSet,
  getTileId,
  getTileGid,
  getTileTransform,
  getTileConfig
} from "tiled-utils";

const FPS = 60;

const Canvas = styled.canvas`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
`;

class GameCanvas extends React.Component {
  lastFrameTimestamp = null;
  frameInterval = null;
  animationTimer = null;

  componentDidMount() {
    this.frameInterval = setInterval(() => this.draw(), 1000 / FPS);
  }

  componentWillUnmount() {
    clearInterval(this.frameInterval);
  }

  draw() {
    const now = Date.now();
    if (this.lastFrameTimestamp) {
      const deltaTime = now - this.lastFrameTimestamp;
      this.drawWorld(deltaTime);
    }

    this.lastFrameTimestamp = now;
  }

  getTileSetImage(tileSet) {
    this.tileSetImageCache = this.tileSetImageCache || {};

    if (!this.tileSetImageCache[tileSet.image]) {
      const { map } = this.props;
      const image = new Image();
      image.src = `${map.basePath}/${tileSet.image}`;

      this.tileSetImageCache[tileSet.image] = image;
    }

    return this.tileSetImageCache[tileSet.image];
  }

  /**
   * Draw a single tile
   */

  drawTile(ctx, position, tileGid) {
    if (!tileGid) return;
    const { map } = this.props;

    const tileSet = getTileSet(map.tilesets, tileGid);
    const tileId = getTileId(tileSet, tileGid);
    const tileConfig = getTileConfig(tileSet, tileGid);

    const { scaleX, scaleY, rotate } = getTileTransform(tileSet, tileGid);

    let image = this.getTileSetImage(tileSet);
    let sx = (tileId % tileSet.columns) * tileSet.tilewidth;
    let sy = Math.floor(tileId / tileSet.columns) * tileSet.tileheight;
    let sWidth = tileSet.tilewidth;
    let sHeight = tileSet.tileheight;
    let dx = position.x;
    let dy = position.y;
    let dWidth = tileSet.tilewidth;
    let dHeight = tileSet.tileheight;

    const hasTransforms = scaleX !== 1 || scaleY !== 1 || rotate !== 0;
    if (hasTransforms) {
      ctx.save();

      ctx.translate(dx + map.tilewidth / 2, dy + map.tileheight / 2);
      dx = -map.tilewidth / 2;
      dy = -map.tileheight / 2;

      ctx.scale(scaleX, scaleY);
      ctx.rotate(rotate);
    }

    if (tileConfig.animation) {
      const duration = tileConfig.animation.reduce(
        (acc, step) => acc + step.duration,
        0
      );
      const animationTiming = this.animationTimer % duration;

      let combinedStepsDuration = 0;
      let animatedTileId;
      for (let i = 0; i < tileConfig.animation.length; i++) {
        combinedStepsDuration += tileConfig.animation[i].duration;

        if (animationTiming < combinedStepsDuration) {
          animatedTileId = tileConfig.animation[i].tileid;
          break;
        }
      }

      sx = (animatedTileId % tileSet.columns) * tileSet.tilewidth;
      sy = Math.floor(animatedTileId / tileSet.columns) * tileSet.tileheight;
    }

    ctx.drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);

    if (hasTransforms) ctx.restore();
  }

  /**
   * Draw the map
   */

  drawMap(ctx, deltaTime) {
    const { map } = this.props;

    const getTilePosition = (containerWidth, index) => ({
      x: (index % containerWidth) * map.tilewidth,
      y: Math.floor(index / containerWidth) * map.tileheight
    });

    map.layers.forEach(layer => {
      if (!layer.visible) return;

      switch (layer.type) {
        case "tilelayer":
          const layerOffset = {
            x: (layer.x + layer.startx) * map.tilewidth + (layer.offsetx || 0),
            y: (layer.y + layer.starty) * map.tileheight + (layer.offsety || 0)
          };

          ctx.save();
          ctx.translate(layerOffset.x, layerOffset.y);

          (layer.data || []).forEach((tileGid, tileIdx) => {
            this.drawTile(ctx, getTilePosition(layer.width, tileIdx), tileGid);
          });

          (layer.chunks || []).forEach(chunk => {
            const chunkOffset = {
              x: (chunk.x - layer.startx) * map.tilewidth,
              y: (chunk.y - layer.starty) * map.tileheight
            };

            ctx.save();
            ctx.translate(chunkOffset.x, chunkOffset.y);

            chunk.data.forEach((tileGid, tileIdx) => {
              this.drawTile(
                ctx,
                getTilePosition(chunk.width, tileIdx),
                tileGid
              );
            });

            ctx.restore();
          });

          ctx.restore();
          break;

        default:
          break;
      }
    });
  }

  /**
   * Draw players
   */

  drawPlayers(ctx, deltaTime) {
    const { map, gameState } = this.props;

    const SPEED_THRESHOLD = 0.1;

    const getTeamGidOffset = team => {
      return (
        {
          yellow: 0,
          green: 1,
          blue: 2,
          red: 3
        }[team] || 0
      );
    };

    (gameState.players || []).forEach(player => {
      ctx.save();
      ctx.translate(Math.round(player.pos.x), Math.round(player.pos.y));

      const speed = Math.hypot(player.vel.x, player.vel.y);
      const isMoving = speed > SPEED_THRESHOLD;

      if (isMoving) {
        const animShift = Math.sin(this.animationTimer / 100);

        ctx.translate(0, Math.abs(animShift * 3));

        ctx.translate(0, 8);
        ctx.rotate(animShift / 10);
        ctx.translate(0, -8);
      }

      if (player.vel.x) ctx.scale(Math.sign(player.vel.x), 1);

      this.drawTile(
        ctx,
        { x: -8, y: -26 },
        getTileGid(map.tilesets, "Custom", 56) + getTeamGidOffset(player.team)
      );
      this.drawTile(
        ctx,
        { x: -8, y: -10 },
        getTileGid(map.tilesets, "Custom", 72) + getTeamGidOffset(player.team)
      );

      ctx.restore();
    });
  }

  /**
   * Draw he whole canvas
   */

  drawWorld(deltaTime) {
    const { map } = this.props;
    if (!this.canvas || !map) return;

    this.animationTimer = Date.now();
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;

    const ctx = this.canvas.getContext("2d");
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
    ctx.webkitImageSmoothingEnabled = false;
    ctx.mozImageSmoothingEnabled = false;
    ctx.imageSmoothingEnabled = false;

    ctx.save();
    ctx.translate(
      Math.floor(this.canvas.width / 2),
      Math.floor(this.canvas.height / 2)
    );

    ctx.scale(3, 3);

    if (
      this.props.gameState &&
      this.props.gameState.players &&
      this.props.gameState.players[0]
    ) {
      const player = this.props.gameState.players[0];
      ctx.translate(Math.round(-player.pos.x), Math.round(-player.pos.y));
    }

    this.drawMap(ctx, deltaTime);
    this.drawPlayers(ctx, deltaTime);

    ctx.restore();
  }

  handleCanvasRef = ref => {
    this.canvas = ref;
  };

  render() {
    return <Canvas width="800" height="600" innerRef={this.handleCanvasRef} />;
  }
}

export default GameCanvas;



